// Media Queries Breakpoints

@mixin mobile {
    @media screen and (min-width: 320px) {
        @content;
    } 
}

@mixin tablet {
    @media screen and (min-width: 767px) {
        @content;
    }
}

@mixin desktop {
    @media screen and (min-width: 1280px) {
        @content;
    }
}
